body {
  margin: 0;
 
  -webkit-font-smoothing: antialiased;
  -moz-osx-font-smoothing: grayscale;
  background-color: bisque;
}

*{
  font-family: 'Inter', system-ui;
}

.wrapper{
  background-color: white;
  box-shadow: 0px 10px 20px black;
  border-radius: 20px;
  max-width: 1080px;
  margin: 50px auto;

}

header{
  border-bottom: 1px solid #eaeaea;
  img{
    margin-right: 15px;
  }
  h3,
  p{
    margin: 0;
  }

  
}

.content {
  h1{
    margin: 0;
  }
}



.button{
  background: #ffffff;
  border: 1px solid #f2f2f2;
  box-sizing: border-box;
  border-radius: 8px;
  height: 32px;
  width: 32px;
  cursor: pointer;
}

.search-block{
  border: 1px solid #f3f3f3;
  border-radius: 10px;
  padding: 0 15px;

  input{
    border: 0px;
    padding: 13px;
    font-size: 16px;
    width: 200px;
  
  }

}


.overlay{
  position: absolute;
  left: 0;
  top: 0;
  width: 100%;
  height: 140% ;
  background-color:rgba(0, 0, 0, 0.5);
  z-index: 1;
}

.drawer{

position: absolute;
width: 420px;
height: 100%;
right: 0;
background: #FFFFFF;
box-shadow: -10px 4px 24px rgba(0, 0, 0, 0.1);
padding: 30px;
display: flex;
flex-direction: column;

  .items{
    flex:1;
    overflow: auto;
    margin-bottom: 40px;
    
    
  }

  h2  {
   margin: 0;
  }
}

.cartEmpty{
  
  p{
font-size: 16px;
line-height: 24px;
text-align: center;
color: grey;


  }
  h1{
    margin: 0;
  }
  button{
    margin-top: 30px
  }
.box{
  margin: 0px;
  width: 120px;
  height: 120px;
  margin-bottom: 20px;
}

  .greanButton{
    position: relative;
  
   &:hover{
     img{
       transform: translateX(-5px);
     }
   }
 
    img{
      
      position: absolute;
      left: 30px;
      top: 20px;
      transition: transform 0.15s ease-in-out;
    }
  }
}


.cartTotalBlock{
  ul{
    display: block;
    margin-bottom: 40px !important;
  
li{
  display: flex;
  align-items: flex-end;
  margin-bottom: 20px;
  div{
    flex: 1;
    height: 1px;
    border-bottom: 1px dashed #dfdfdf;
    position: relative;
    top: -4px;
    margin: 0 7px;
  }
}
  }

 .greanButton{
   position: relative;
 
  &:disabled{
    background-color: #bebebe;
    cursor: default;
  }

  &:hover{
    img{
      transform: translateX(5px);
    }
  }

   img{
     position: absolute;
     right: 30px;
     top: 20px;
     transition: transform 0.15s ease-in-out;
   }
 }
}

.greanButton{
    width: 100%;
    height: 55px;
    background: #9DD558;
    border-radius: 18px;
    border: 0px;
    color: white;
    font-size: 16px;
    font-weight: 500;
    cursor: pointer;
    transition: background 0.1s ease-in-out;
  
    



    &:hover{
      background: lighten(#9DD558, 5%);
    }
  
    &:active{
      background: drawer( #9DD558, -5%);
    }
    
  
  
}

.cartItem{
  
  border: 1px solid #f3f3f3;
  border-radius: 20px;
  overflow: hidden;
  padding: 10px;

  .cartItemImg{
    height: 70px;
    width: 70px;
    background-size: contain;
    background-position: 0 -3px;
    background-repeat: no-repeat;
    margin-right: 20px;
  }


  p{
    font-size: 14px;
    margin: 0;
  }
  b{
    font-size: 16px;
  }

.removeBtn{
  opacity: 0.5;
  cursor: pointer;
  transition: opacity 0.15s ease-in-out;

  &:hover{
    opacity: 1;
  }
}

}